.lesson{
  &__header{
    &-wrapper{
      display: grid;
      grid-template-columns: auto 415px;

      gap: 126px;
    }

    &-img{
      width: 100%;
      height: 240px;

      border: 2px solid #6776FF;
      border-radius: 20px;

      display: flex;
      align-items: center;
      justify-content: center;

      padding: 5px 58px;
      &_mobile{
        display: none;
      }
      img{
        width: 100%;
        height: 100%;

        object-fit: contain;
      }
    }
  }
  &__banner{
    margin: 120px 0 0 0;
    &-wrapper{
      background: #F9F9FF;
    }
  }
  &-header{
    &__title{
      margin: 16px 0 0 0;
    }
    &__info{
      margin: 40px 0 0 0;
    }
    &__favorite{
      margin: 64px 0 0 0;

      display: flex;
      align-items: flex-start;

      &-btn{
        width: 255px;

        margin: 0 0 0 48px;

        padding: 24px 0 0 0;
        .button{
          width: 100%;
        }
      }
    }
  }
  &-banner{
    &__wrapper{
      display: flex;
      justify-content: space-between;
      height: 185px;
      &-img{
        width: 370px;
        height: 270px;

        flex-shrink: 0;

        transform: translateY(-47px);

        img{
          width: 100%;
          height: 100%;
          object-fit: contain;
        }
      }
      &-content{
        margin: 0 0 0 140px;

        align-self: center;

        max-width: 330px;
        flex-shrink: 0;
      }
      &-title{
        font-family: 'Gilroy', sans-serif;
        font-weight: 700;
        font-size: 16px;
        line-height: 20px;
      }
      &-button{
        margin: 16px 0 0 0;

        box-shadow: 0px 4px 6px rgba(0, 35, 103, 0.08);
        width: 255px;
      }
      &-social{
        margin-left: auto;

        align-self: flex-end;

        padding-bottom: 40px;
      }
    }
    &__social{
      &-title{
        font-family: 'Graphik LCG', sans-serif;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;

        color: #2F2F2F;
      }
      &-list{
        display: flex;
        align-items: center;
        justify-content: flex-end;

        margin: 17px 0 0 0;
      }
      &-item{
        & + &{
          margin: 0 0 0 17px;
        }
        display: block;
        width: 45px;
        height: 45px;
      }
    }
  }
}

.lesson{
  &-info{
    &__wrapper{
      display: flex;
      align-items: center;
      &-block{
        display: flex;
        align-items: center;
        & + &{
          margin: 0 0 0 48px;
        }
      }
    }
    &__icon{
      width: 24px;
      height: 24px;
    }
    &__content{
      margin: 0 0 0 12px;
    }
    &__text{
      font-family: 'Graphik LCG', sans-serif;
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;
    }
  }
  &-tags{
    &__wrapper{
      display: flex;
      align-items: center;
      &-item{
        & + &{
          margin: 0 0 0 76px;
        }
      }
    }
  }
  &-tag{
    &__wrapper{
      display: flex;
      align-items: flex-start;
    }
    &__icon{
      width: 33px;
      height: 33px;
      flex-shrink: 0;
    }
    &__content{
      margin: 0 0 0 12px;
    }
    &__title{
      font-family: 'Graphik LCG', sans-serif;
      font-weight: 400;
      font-size: 14px;
      line-height: 17px;

      color: #2F2F2F;
    }
    &__text{
      margin: 7px 0 0 0;

      font-family: 'Gilroy', sans-serif;
      font-weight: 700;
      font-size: 16px;
      line-height: 20px;
    }
  }

  &-description{
    &__block{
      &-content{
        margin: 16px 0 0 0;
      }
      &-text{
        margin: 32px 0 0 0;
      }
    }
  }

  &__tags{
    margin: 105px 0 0 0;
  }

  &__description{
    margin: 72px 0 0 0;
  }

  &__desc{
    font-family: 'Gilroy', sans-serif;
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;

    color: #2F2F2F;

    max-width: 738px;
  }

  &__text{
    font-family: 'Graphik LCG', sans-serif;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;

    color: #2F2F2F;
    &-small{
      display: flex;
      flex-direction: column;
      flex-wrap: wrap;
      max-height: 160px;

      max-width: 630px;
    }
    p + p{
      margin: 20px 0 0 0;
    }
    ul{
      list-style: disc;

      margin-left: 25px;
    }
  }
}

.favorite{
  &-block{
    &__wrapper{
      height: 146px;
      width: 414px;

      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;

      padding: 24px 40px;

      border-radius: 20px;
      overflow: hidden;

      background: url("../img/favorite-bg.svg") center center/cover no-repeat;
    }
    &__button{
      max-width: 322px;
      width: 100%;
      &-item{
        width: 100%;
        font-weight: 600;

        box-shadow: 0px 2px 6px rgba(0, 35, 103, 0.05);

        &::after{
          content: '';
          width: 20px;
          height: 20px;

          display: block;

          margin: 0 0 0 10px;

          background: url("../img/star-empty.svg") center center/contain no-repeat;
        }
      }
    }
    &__button + &__content{
      margin: 16px 0 0 0;
    }
    &__content{
      max-width: 321px;
    }
    &__text{
      font-family: 'Graphik LCG', sans-serif;
      font-weight: 400;
      font-size: 14px;
      line-height: 17px;
    }
  }
}

@media screen and (max-width: 1280px) {
  .lesson{
    &__header{
      &-wrapper{
        gap: 0;
        grid-template-columns: repeat(2,minmax(0,1fr));
      }
    }
    &__text{
      &-small{
        max-height: unset;
        max-width: unset;
      }
    }
    &-banner{
      &__wrapper{
        &-content{
          margin: 0 0 0 30px;
        }
      }
    }
    &-tags{
      &__wrapper{
        &-item{
           & + &{
             margin: 0 0 0 15px;
           }
        }
      }
    }
    &-header{
      &__favorite{
        flex-direction: column;
        &-btn{
          margin: 24px 0 0 0;
          padding: 0;
        }
      }
    }
  }
}

@media screen and (max-width: 991px) {
  .lesson{
    &__header{
      &-wrapper{
        grid-template-columns: minmax(0,1fr);
      }
      &-block{
        &:nth-child(2){
          display: none;
        }
      }
      &-img{
        margin: 24px 0 0 0;
        &_mobile{
          display: flex;
        }
      }
    }
    &__tags{
      margin: 88px 0 0 0;
    }
    &__description{
      margin: 48px 0 0 0;
    }
    &__banner{
      margin: 88px 0 0 0;
    }
    &-header{
      &__info{
        margin: 24px 0 0 0;
      }
      &__favorite{
        margin: 32px 0 0 0;
      }
    }
    &-tags{
      &__wrapper{
        display: grid;
        grid-template-columns: repeat(2,minmax(0,1fr));
        gap: 15px;
        &-item{
           & + &{
             margin: 0;
           }
        }
      }
    }
    &-banner{
      &__social{
        display: flex;
        align-items: center;
        &-list{
          margin: 0 0 0 24px;
        }
      }
      &__wrapper{
        flex-direction: column;

        height: auto;

        padding: 40px 0 ;

        &-content{
          order: 1;
          margin: 0;
        }
        &-img{
          order: 2;

          transform: translateY(0) translateX(-40px);

          margin: 16px 0;

          width: calc(100% + 80px);
          height: auto;
        }
        &-social{
          order: 3;

          align-self: center;
          justify-self: center;

          padding: 0;
        }
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .lesson{
    &-info{
      &__wrapper{
        flex-direction: column;
        align-items: flex-start;
        &-block{
          & + &{
            margin: 16px 0 0 0;
          }
        }
      }
    }
    &-header{
      &__favorite{
        &-btn{
          margin: 20px auto 0 auto;
        }
      }
    }

    &-tags{
      &__wrapper{
        grid-template-columns: minmax(0,1fr);
        gap: 20px;
      }
    }
  }
  .favorite{
    &-block{
      &__wrapper{
        width: 100%;

        padding: 16px;
      }
    }
  }
}