body{
  color:#2F2F2F;
  &.no-scroll-m{
    overflow: hidden;
  }
}

.page{
  &-wrapper{
    min-height: 100vh;
    display: flex;
    flex-direction: column;

    &__content{
      height: 100%;
    }
    &__footer{
      margin-top: auto;
    }
  }
  &-header{
    &__wrapper{
      padding: 40px 0 0 0;
      &-big{
        height: 210px;
        background: url("../img/page-header-bg.svg") center bottom/cover no-repeat;
      }
    }
    &__title{
      margin: 5px 0 0 0;
    }
  }
  &-catalog{
    &__wrapper{
      padding: 0 0 80px 0;
    }
    &__content{
      display: grid;
      grid-template-columns: 275px auto;
      gap: 50px;

      margin: 24px 0 0 0;
    }
    &__sort{
      display: flex;
      justify-content: flex-end;

      margin: 5px 0 0 0;
    }
    &__card{
      display: grid;
      grid-template-columns: repeat(3,minmax(0,1fr));
      gap: 16px;

      width: 100%;
      &-item{
        background: #FFFFFF;
        box-shadow: 0px 0px 15px rgba(7, 14, 77, 0.1);
        border-radius: 16px;
      }
    }
    &__search{
      margin: 48px 0 0 0;
    }
  }
  &-lesson{
    &__wrapper{
      padding: 0 0 75px 0;
    }
  }
}

.container{
  max-width: 1360px;
  width: 100%;

  padding: 0 40px;

  margin: 0 auto;
}

.icon{
  width: 100%;
  height: 100%;
}

.title{
  &_size{
    &-big{
      font-family: 'Gilroy', sans-serif;
      font-size: 44px;
      line-height: 50px;
      font-weight: 700;
    }
    &-medium{
      font-family: 'Gilroy',sans-serif;
      font-weight: 700;
      font-size: 41px;
      line-height: 51px;
    }
    &-smallest{
      font-family: 'Gilroy', sans-serif;
      font-weight: 700;
      font-size: 31px;
      line-height: 38px;
    }
    &-small{
      font-family: 'Gilroy', sans-serif;
      font-weight: 700;
      font-size: 16px;
      line-height: 20px;
    }
  }
  &_color{
    &-white{
      color:#fff;
    }
  }
  &_position{
    &-center{
      text-align: center;
    }
  }
}

.button{
  -webkit-appearance: none;
  border: none;
  outline: none;
  display: flex;
  align-items: center;
  justify-content: center;

  height: 48px;
  width: 205px;

  font-family: 'Gilroy', sans-serif;
  font-weight: 600;
  font-size: 16px;
  line-height: 20px;
  &-white{
    background: #FFFFFF;

    font-family: 'Gilroy',sans-serif;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;

    color: #5465FF;
  }
  &-blue{
    font-family: 'Gilroy', sans-serif;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;

    color: #FFFFFF;

    background: #6776FF;
  }
  &-bordered{
    border-radius: 40px;
  }
}

.breadcrumb{
  &__list{
    display: flex;
    align-items: center;
  }
  &-list{
    &__item{
      color:#6776FF;
      &_white{
        color:#fff;
      }
    }
    &__link{
      font-family: 'Graphik LCG', sans-serif;
      font-weight: 400;
      font-size: 14px;
      line-height: 14px;
      color: inherit;
    }
    &__item{
      display: flex;
      align-items: center;
      & + &{
        &::before{
          content: '';
          width: 12px;
          height: 12px;
          display: block;

          margin: 0 8px;

          background-size: contain;
          background-position: center center;
          background-repeat: no-repeat;

          background-image: url("../img/breadcrumb-arrow-blue.svg");
        }
      }
      &_white{
        & + &{
          &::before{
            background-image: url("../img/breadcrumb-arrow-white.svg");
          }
        }
      }
    }
  }
}

.sort{
  &__wrapper{
    position: relative;
    display: flex;
    align-items: center;
  }
  &__item{
    position: relative;
    & + &{
      margin:0 0 0 40px;
    }
    &-mobile{
      display: none;
    }
  }
  &__link{
    display: flex;
    align-items: center;
    &-icon{
      width: 20px;
      height: 20px;
      display: block;

      margin: 0 0 0 12px;
    }
    &-text{
      font-family: 'Graphik LCG', sans-serif;
      font-weight: 400;
      font-size: 16px;

      color: #2F2F2F;
    }
  }
  &__content{
    position: absolute;
    right: 12px;
    top: calc(100% + 12px);

    display: none;
    &.active{
      display: block;
    }
  }
  &__list{
    width: 210px;

    background: #FFFFFF;
    box-shadow: 0px 2px 20px rgba(7, 14, 77, 0.15);
    border-radius: 10px;
    &-item{
      & + &{
        border-top: 1px solid #E3E3E3;
      }
    }
    &-link{
      display: flex;
      align-items: center;

      padding: 16px 24px 12px 24px;

      font-family: 'Graphik LCG', sans-serif;
      font-weight: 400;
      font-size: 14px;
      color: #2F2F2F;
    }
  }
}

.cookie-alert{
  position: fixed;
  right: calc(50% - 680px);
  bottom: 150px;
  &.hidden{
    display: none;
  }
  &__wrapper{
    max-width: 435px;
    width: 100%;
    padding: 16px 32px 24px 32px;

    background: rgba(255, 163, 25, 0.8);
    box-shadow: 0px -4px 20px rgba(0, 0, 0, 0.05);
    border-radius: 27px;
  }
  &__text{
    font-family: 'Graphik LCG', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;

    color: #FFFFFF;
  }
  &__footer{
    margin: 12px 0 0 0;

    display: flex;
    align-items: center;
    &-block + &-block{
      margin: 0 0 0 24px;
    }
  }
  &__button{
    width: 120px;
    height: 50px;
    box-shadow: 0px 2px 6px rgba(0, 35, 103, 0.05);
  }
  &__link{
    font-family: 'Graphik LCG',sans-serif;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    text-decoration-line: underline;

    color: #FFFFFF;
  }
}

@media screen and (max-width: 1360px) {
  .cookie-alert{
    right: 0;
  }
}

@media screen and (max-width: 1280px) {
  .page{
    &-catalog{
      &__card{
        grid-template-columns: repeat(2,minmax(0,1fr));
      }
    }
  }
}

@media screen and (max-width: 991px) {
  .page{
    &-wrapper{
      padding: 64px 0 0 0;
    }
    &-catalog{
      &__card{
        grid-template-columns: minmax(0,1fr);
      }
      &__content{
        grid-template-columns: minmax(0,1fr);
        &-filter{
          position: fixed;
          top: 64px;
          left: 0;
          width: 100%;
          height: calc(100% - 64px);

          background: #FFFFFF;
          box-shadow: 0px 0px 10px rgba(7, 14, 77, 0.1);
          border-radius: 0px 0px 30px 30px;

          padding: 50px 40px 0 40px;

          overflow-x: hidden;
          overflow-y: auto;

          display: none;
          &.active{
            display: block;
          }
        }
      }
      &__sort{
        margin: 40px 0 0 0;
      }
      &__search{
        margin: 32px 0 0 0;
      }
    }
    &-header{
      &__wrapper{
        &-big{
          background: url("../img/page-header-bg-m.svg") center bottom/cover no-repeat;

          height: 190px;
        }
      }
      &__title{
        margin: 24px 0 0 0;
      }
    }
  }
  .sort{
    &__item{
      &-mobile{
        display: block;
      }
    }
    &__link{
      &-text{
        display: none;
      }
      &-icon{
        margin: 0;
      }
    }
  }
}

@media screen and (max-width: 767px) {
  .title{
    &_size{
      &-big{
        font-size: 28px;
        line-height: 30px;
      }
      &-medium{
        font-size: 28px;
        line-height: 30px;
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .cookie-alert{
    bottom: 15px;
    right: -40px;

    width: 100%;
    &__wrapper{
      max-width: calc(100% - 80px);
      width: 100%;

      padding: 16px;

      background: rgba(255, 163, 25, 0.9);
      box-shadow: 0px -4px 20px rgba(0, 0, 0, 0.05);
      border-radius: 22px;
    }
    &__text{

    }
  }
}